<template>
  <el-container class="detail">
    <el-aside width="268px">
      <aside-card
        :cardType="1"
        title=""></aside-card>
      <aside-card
        :cardType="2"
        title=""></aside-card>
      <aside-card
        :cardType="3"
        title=""></aside-card>
      <aside-card
        :cardType="4"
        title=""></aside-card>
    </el-aside>
    <el-main class="content">
      <div class="detail-header">
        <div class="title-box clearfix">
          <el-tag class="fl"></el-tag>
          <h2 class="title ellipsis">Express4 </h2>
        </div>
        <div class="detail-info clearfix">
          <div class="info-item">2019-10-17 07:52:26</div>
          <router-link to="/about" class="info-item item-author"></router-link>
          <div class="info-item">views: 9</div>
          <el-button class="fr" icon="el-icon-edit" size="small"></el-button>
          <el-button class="fr" size="small">star: 9</el-button>
        </div>
        <prev-and-next></prev-and-next>
      </div>
      <div class="detail-content">
        <div class="article-content" v-html="markdownHtml"></div>
        <prev-and-next></prev-and-next>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import AsideCard from './../components/AsideCard.vue'
import prevAndNext from './../components/PrevAndNext.vue'
import 'highlight.js/scss/default.scss'
import 'highlight.js/styles/vs2015.css'
import hljs from 'highlight.js/lib/highlight'
import javascript from 'highlight.js/lib/languages/javascript'
import MarkdownIt from 'markdown-it'
hljs.registerLanguage('javascript', javascript)
let md = new MarkdownIt({
  highlight: function (str, lang) {
    if (lang && hljs.getLanguage(lang)) {
      try {
        return '<pre class="hljs"><code>' +
               hljs.highlight(lang, str, true).value +
               '</code></pre>'
      } catch (__) {}
    }

    return '<pre class="hljs"><code>' + md.utils.escapeHtml(str) + '</code></pre>'
  }
})

export default {
  name: 'detail',
  data () {
    return {
      content: ` mysql 

## 
 mysql `
    }
  },
  components: {
    AsideCard,
    prevAndNext
  },
  computed: {
    markdownHtml () {
      return this.content && md.render(this.content)
    }
  }
}
</script>

<style lang="scss" scoped>
@import './../style/views/home.scss';
